import { Component, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { PurchaseOrderService } from 'src/app/api/controllers/PurchaseOrder';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-inventory-receiving',
  templateUrl: './inventory-receiving.component.html',
  styleUrls: ['./inventory-receiving.component.scss']
})
export class InventoryReceivingComponent implements OnInit {


  public asset:any = {};
  public assetSettings:any = {};
	isAssetLoaded: boolean;
	warehouseData: any;
  

  ngOnInit(): void {
	  this.getAllGetAllWarehouse();
    this.assetSettings = {
			singleSelection: true,
			text: "Search",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			badgeShowLimit: 3,
			// lazyLoading: true
		  };
  }

  constructor(

	private cookieService: CookieService,
	private purchaseOrderService: PurchaseOrderService,
	private sharedService: SharedService,
) {
}
	onItemSelect(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	  }
	  OnItemDeSelect(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	  }
  selectedItems(selectedItems: any) {
    throw new Error("Method not implemented.");
  }
	  onSelectAll(items: any) {
		console.log(items);
	  }
	  onDeSelectAll(items: any) {
		console.log(items);
	  }

	  getAllGetAllWarehouse() {
		let queryParamBase: any = {};
		queryParamBase = {
		  apartmentId: this.cookieService.get('apartmentId')
		};
		this.isAssetLoaded = true;
		this.purchaseOrderService.getAllWarehouseByApartmentId(queryParamBase).subscribe((res: any) => {
		  if (res) {
			// this.warehouseData = [];
			this.warehouseData = res && res.length > 0 ? res : [];
			}
		});
	  }
}
