	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}


	filterCategoryItem(value) {
		this.inventoryCategoryList = this.tempCategoryList.filter((item: any) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1
		);
	}

	filterWareHouse(value) {
		this.warehouseData = this.tempWarehouseData.filter((item: any) =>
			item.warehouseName.toString().toLowerCase().indexOf(value.toLowerCase()) > -1);
	}
	addSetUp(name, type, value, e): void {
		console.log(type, value);
		const dialogRef = this.dialog.open(AssetsAddSetupComponent, {
			width: '70%',
			data: {
				'name': name,
				'type': type,
				'value': JSON.stringify(value)
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log('The dialog was closed');
			console.log(result);
			if (result) {
				this.getAllInventory();
			}
		});
	}



